<template>
    <v-container fluid>
        <v-layout>
            <h1>Room View</h1>
            <br />
            <v-btn color="primary" @click="showSnapshot(14134)">
                Get Snapshot
            </v-btn>
            <br />
            <p>{{ urltest }}</p>
            <br />
            <pre>{{ snapshot }}</pre>
            <br />
        </v-layout>
    </v-container>
</template>
<script>
export default {
    name: 'Room',
    methods: {
        showSnapshot(id) {
            this.$store.dispatch('getSnapshot', id);
        }
    },
    computed: {
        snapshot() {
            return this.$store.state.snapshot;
        },
        urltest() {
            return window.location;
        }
    }
};
</script>
<style scoped></style>
